<script>
import { mapGetters, mapState } from "vuex";
import SearchSelector from "./SearchSelector";
export default {
  name: "Search",
  components: {
    SearchSelector,
  },
  data() {
    return {
      searchParams: {
        category1Id: "",
        category2Id: "",
        category3Id: "",
        categoryName: "",
        keyword: "",
        props: [],
        trademark: "",

        order: "1:asc",
        pageNo: 14,
        pageSize: 10,
      },
    };
  },

  // 在点击三级分类或者点击搜索按钮调过来发请求之前，把对应的三级分类名称和id或者关键字keyword，拿到添加到data中的searchParams中
  beforeMount() {
    this.handleSearchParams();
  },

  mounted() {
    this.getSearchInfo();
  },

  // 当我们已经在search页面的时候，再点击搜索发现页面没再发请求，这是因为咱们的请求只在mounted的时候发过，然后组件没被销毁，所以就有这个bug
  // 我们可以监视路由对象，当他发生变化时，我们再发送一次请求就好了
  watch: {
    $route: {
      handler(newVal, oldVal) {
        this.handleSearchParams();
        this.getSearchInfo();
      },
    },
  },

  methods: {
    // 发送请求
    getSearchInfo() {
      this.$store.dispatch("getSearchInfo", this.searchParams);
    },

    // 封装请求参数的函数
    handleSearchParams() {
      let { category1Id, category2Id, category3Id, categoryName } =
        this.$route.query;
      let { keyword } = this.$route.params;

      let searchParams = {
        ...this.searchParams,
        category1Id,
        category2Id,
        category3Id,
        categoryName,
        keyword,
      };

      this.searchParams = searchParams;
    },

    removeCategoryName() {
      (this.searchParams.categoryName = ""),
        // this.getSearchInfo();     // 这里删除以后不会动我的原来路径，所这样发请求不行，我们得让路径发生变化
        this.$router.push({ name: "search", params: this.$route.params });
    },

    removeKeyword() {
      (this.searchParams.keyword = ""), this.$bus.$emit("clearKeyword"); //通知header组件清空搜索的关键词
      // this.getSearchInfo();         // 这里删除以后不会动我的原来路径，所这样发请求不行，我们得让路径发生变化
      this.$router.push({ name: "search", query: this.$route.query });
    },

    // 根据品牌搜索
    searchForTrademark(trademark) {
      console.log(trademark);
    },

    // 改变排序规则
    changeOrder(sortFlag) {
      let originSortFlag = this.sortFlag;
      let originSortType = this.sortType;
      let newOrder = "";

      // 如果点击了相同的排序标志，说明点击的是同一个标志，我们需要将排序类型改变
      if (sortFlag === originSortFlag) {
        newOrder = `${sortFlag}:${originSortType === "asc" ? "desc" : "asc"}`;
      } else {
        // 如果点击了不同的排序标志，说明点击的是同一个标志，我们需要将排序类型改变
        newOrder = `${sortFlag}:desc`;
      }
      console.log(newOrder);
      this.searchParams.order = newOrder;
    },
  },
  computed: {
    ...mapGetters(["attrList", "goodList", "trademarkList"]),
    ...mapState({
      searchInfo:state=>state.search.searchInfo
    }),
    sortFlag() {
      return this.searchParams.order.split(":")[0];
    },
    sortType() {
      return this.searchParams.order.split(":")[1];
    },
  },
};
</script>

